import React, { useState } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  Typography,
  Box,
  CircularProgress,
  Alert,
  Paper,
  List,
  ListItem,
  ListItemText,
  Divider
} from '@mui/material';
import { Anime } from '../types';

interface BulkAnimeFormProps {
  open: boolean;
  onClose: () => void;
  onSave: (animes: Omit<Anime, 'id'>[]) => void;
}

const BulkAnimeForm: React.FC<BulkAnimeFormProps> = ({ open, onClose, onSave }) => {
  const [titlesText, setTitlesText] = useState<string>('');
  const [isProcessing, setIsProcessing] = useState<boolean>(false);
  const [processedTitles, setProcessedTitles] = useState<string[]>([]);
  const [error, setError] = useState<string | null>(null);

  // 
  const handleTitlesChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setTitlesText(e.target.value);
    setError(null);
  };

  // 
  const resetForm = () => {
    setTitlesText('');
    setProcessedTitles([]);
    setError(null);
    setIsProcessing(false);
  };

  // 
  const handleClose = () => {
    resetForm();
    onClose();
  };

  // 
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    // 
    const titles = titlesText
      .split('\n')
      .map(title => title.trim())
      .filter(title => title.length > 0);
    
    if (titles.length === 0) {
      setError('1');
      return;
    }

    setIsProcessing(true);
    setProcessedTitles([]);

    // 
    const animesToAdd: Omit<Anime, 'id'>[] = titles.map(title => ({
      title,
      year: undefined,
      genres: [],
      personalRating: 3,
      notes: '',
      watchedDate: new Date().toISOString().split('T')[0],
      coverImage: ''
    }));

    // 
    setProcessedTitles(titles);
    onSave(animesToAdd);
    resetForm();
  };

  return (
    <Dialog open={open} onClose={handleClose} maxWidth="md" fullWidth>
      <DialogTitle></DialogTitle>
      <form onSubmit={handleSubmit}>
        <DialogContent>
          <Typography variant="body1" gutterBottom>
            11
            
          </Typography>
          
          <TextField
            autoFocus
            margin="dense"
            label="11"
            multiline
            rows={10}
            fullWidth
            variant="outlined"
            value={titlesText}
            onChange={handleTitlesChange}
            placeholder="\n\nSPYFAMILY\n"
            disabled={isProcessing}
            error={!!error}
            helperText={error}
          />

          {processedTitles.length > 0 && (
            <Box mt={2}>
              <Typography variant="subtitle1"></Typography>
              <Paper variant="outlined" sx={{ maxHeight: 200, overflow: 'auto', mt: 1 }}>
                <List dense>
                  {processedTitles.map((title, index) => (
                    <React.Fragment key={index}>
                      <ListItem>
                        <ListItemText primary={title} />
                      </ListItem>
                      {index < processedTitles.length - 1 && <Divider />}
                    </React.Fragment>
                  ))}
                </List>
              </Paper>
            </Box>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} disabled={isProcessing}>
            
          </Button>
          <Button 
            type="submit" 
            variant="contained" 
            color="primary"
            disabled={isProcessing || !titlesText.trim()}
          >
            {isProcessing ? (
              <>
                <CircularProgress size={24} sx={{ mr: 1 }} />
                ...
              </>
            ) : ''}
          </Button>
        </DialogActions>
      </form>
    </Dialog>
  );
};

export default BulkAnimeForm;
